body {
  font-family: 'proxima-nova', sans-serif;
  font-size: 18px;
  color: #30302F;
  line-height: 1.3;
}

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5 {
  font-family: 'intro-bold', sans-serif;
  color: #111110;
}

.title-h1--white,
.title-h2--white,
.title-h3--white,
.title-h4--white,
.title-h5--white {
  color: #fff;
}

.title-h1 {
  font-size: 40px;
  line-height: 1.43;
}

.title-h2 {
  font-size: 35px;
}

.title-h3 {
  font-size: 30px;
}

.title-h4 {
  font-size: 25px;
}

.title-h5 {
  font-size: 20px;
}

.section-title {
  text-align: center;
}

.section-title:after {
  content: "";
  display: block;
  width: 50px;
  height: 1px;
  margin: 15px auto 0;
  background: #FF007C;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 85px;
  padding: 30px 15px 15px;
  min-height: 685px;
  background-size: cover;
  background-position: 50% 35%;
}

.hero__title {
  max-width: 615px;
  margin: 0 auto;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.hero__highlight {
  display: block;
  padding: 5px 0;
  background: #FDD80C;
  color: #111110;
  line-height: 1.2;
}

.category {
  padding: 0 15px;
}

.category__title {
  margin-bottom: 65px;
}

.category__inner {
  max-width: 1160px;
  margin: 0 auto 100px;
  padding: 0 25px;
}

.category__block {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -25px;
}

.category__element {
  flex-basis: 50%;
  padding: 0 25px 50px;
}

.card {
  position: relative;
  max-width: 530px;
}

.card:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.card__inner {
  position: relative;
}

.card__mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.card__wave {
  position: absolute;
  top: -50px;
  left: -65px;
}

.card__title {
  position: relative;
  padding: 20px 92px 0 14px;
  color: #fff;
}

.card__subtitle {
  font-size: 16px;
  line-height: 1.35;
}

.card__pic {
  display: block;
  width: 100%;
  height: 330px;
  object-fit: cover;
  font-size: 'object-fit: cover';
}

.card__price {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 18px 20px;
  background: #FDD80C;
  font-family: 'proxima-nova-bold', sans-serif;
  font-size: 23px;
  line-height: 1;
}

.card__description {
  position: relative;
  display: flex;
  height: 60px;
  align-items: center;
  padding: 5px 14px;
}

.card__description span {
  display: inline-block;
  max-height: 50px;
  overflow: hidden;
}

.calendar {
  position: absolute;
  top: 3px;
  right: 14px;
  width: 78px;
  padding: 16px 0;
  text-align: center;
  background: #fff;
  box-shadow: 0px 4px 5px rgba(48, 48, 47, 0.25);
  border-radius: 5px;
  font-size: 15px;
}

.calendar__date {
  display: block;
  font-family: 'proxima-nova-semibold', sans-serif;
  font-size: 18px;
}

.calendar__stroke {
  position: absolute;
  top: -7px;
  left: 50%;
  width: 35px;
  height: 13px;
  margin-left: -17.5px;
  border-right: 1px solid #30302F;
  border-left: 1px solid #30302F;
}

.calendar__stroke:before {
  content: '';
  position: absolute;
  left: 50%;
  width: 1px;
  height: 13px;
  margin-left: -0.5px;
  background: #30302F;
}
