.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 85px;
  padding: 30px 15px 15px;
  min-height: 685px;
  background-size: cover;
  background-position: 50% 35%;
  &__title {
    max-width: 615px;
    margin: 0 auto;
    text-align: center;
    text-transform: uppercase;
    color: #fff; }
  &__highlight {
    display: block;
    padding: 5px 0;
    background: $yellow;
    color: $black;
    line-height: 1.2; } }
