.category {
  padding: 0 15px;
  &__title {
    margin-bottom: 65px; }
  &__inner {
    max-width: 1160px;
    margin: 0 auto 100px;
    padding: 0 25px; }
  &__block {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -25px; }
  &__element {
    flex-basis: 50%;
    padding: 0 25px 50px; } }

.card {
  position: relative;
  max-width: 530px;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.2); }
  &__inner {
    position: relative; }
  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden; }
  &__wave {
    position: absolute;
    top: -50px;
    left: -65px; }
  &__title {
    position: relative;
    padding: 20px 92px 0 14px;
    color: #fff; }
  &__subtitle {
    font-size: 16px;
    line-height: 1.35; }
  &__pic {
    display: block;
    width: 100%;
    height: 330px;
    object-fit: cover;
    font-size: 'object-fit: cover'; }
  &__price {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 18px 20px;
    background: $yellow;
    @include proxima-b;
    font-size: 23px;
    line-height: 1; }
  &__description {
    position: relative;
    display: flex;
    height: 60px;
    align-items: center;
    padding: 5px 14px;
    span {
      display: inline-block;
      max-height: 50px;
      overflow: hidden; } } }


.calendar {
  position: absolute;
  top: 3px;
  right: 14px;
  width: 78px;
  padding: 16px 0;
  text-align: center;
  background: #fff;
  box-shadow: 0px 4px 5px rgba(48, 48, 47, 0.25);
  border-radius: 5px;
  font-size: 15px;
  &__date {
    display: block;
    @include proxima-sb;
    font-size: 18px; }
  &__stroke {
    position: absolute;
    top: -7px;
    left: 50%;
    width: 35px;
    height: 13px;
    margin-left: -17.5px;
    border-right: 1px solid $dark-brown;
    border-left: 1px solid $dark-brown;
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      width: 1px;
      height: 13px;
      margin-left: -0.5px;
      background: $dark-brown; } } }
